<template>
  <div v-if="!waitingResponse">
    <v-row>
      <v-col cols="2" @click="restart">
        <v-btn class="ma-5" @click="restart" color="error">Restart only</v-btn>
      </v-col>
    </v-row>
    <v-divider></v-divider>
    <v-row>
      <v-btn class="ma-5" color="primary" @click="createStudents"
        >Create Students</v-btn
      >
      <v-btn class="ma-5" color="primary" @click="createTeachers"
        >Create Teachers</v-btn
      >
      <v-btn class="ma-5" color="primary" @click="createCourse"
        >Create Course</v-btn
      >
    </v-row>
    <v-divider></v-divider>
    <v-row>
      <v-col cols="2">
        <v-select
          label="Offers"
          outlined
          item-text="code"
          item-value="id"
          v-model="selectedOffer"
          :items="offers"
        >
        </v-select>
      </v-col>
      <v-col cols="2">
        <v-btn @click="createHomework" color="primary">Create Homework</v-btn>
      </v-col>
    </v-row>
    <v-divider></v-divider>
    <v-row>
      <v-col cols="2">
        <v-select
          label="Homeworks"
          outlined
          item-text="name"
          item-value="id"
          v-model="selectedHomework"
          :items="homeworks"
        >
        </v-select>
      </v-col>
      <v-btn class="ml-5 mt-5" color="primary" @click="answerAssignments"
        >Answer</v-btn
      >
      <v-btn class="ml-5 mt-5" color="primary" @click="reviewAssignments"
        >Review</v-btn
      >
    </v-row>
  </div>
</template>

<script>
import SchoolService from "../services/SchoolService";
import CourseService from "../services/CourseService";
import OfferService from "../services/OfferService";
import HomeworkService from "../services/HomeworkService";
import axios from "axios";
import StudentService from "../services/StudentService";

export default {
  data() {
    return {
      dummyCourseNames: [
        "Algorithms",
        "Data Structures",
        "Philosophy",
        "Calculus",
        "History",
        "Physics",
        "English"
      ],
      homeworkAmount: 5,
      questionAmount: 5,
      offers: [],
      teachers: [],
      homeworks: [],
      selectedOffer: Number,
      selectedHomework: Number,
      isCreated: false,
      waitingResponse: false
    };
  },
  created() {
    OfferService.GetOffers(1).then(offers => {
      this.offers = offers;
    });
    SchoolService.GetTeachers(1).then(teachers => {
      this.teachers = teachers;
    });
    SchoolService.GetHomeworks(1).then(homeworks => {
      this.homeworks = homeworks;
    });
  },
  methods: {
    restart() {
      this.waitingResponse = true;
      SchoolService.Restart()
        .then(() => {
          return SchoolService.CreateSchool({
            name: "Çukurova Üniversitesi",
            email: "ozhanefemeral@gmail.com",
            password: "efemeral1"
          });
        })
        .then(() => {
          return SchoolService.LoginSchool(
            "ozhanefemeral@gmail.com",
            "efemeral1"
          );
        })
        .then(response => {
          this.$emit("logged", response.token, "school");
          this.waitingResponse = false;
        });
    },

    async createCourse() {
      this.waitingResponse = true;
      let rndIndex = Math.floor(Math.random() * this.dummyCourseNames.length);
      const courseBody = {
        name: this.dummyCourseNames[rndIndex],
        schoolId: 1
      };

      CourseService.CreateCourse(courseBody).then(course => {
        let createdOffer;
        OfferService.CreateOffer({
          courseId: course.id,
          teacherId: this.teachers[0].id,
          semester: "Summer",
          code: courseBody.name.slice(0, 3) + course.id
        })
          .then(offer => {
            createdOffer = offer;
            return StudentService.GetStudents();
          })
          .then(students => {
            let enrollPromises = [];
            for (let i = 0; i < students.length; i++) {
              let currentStudent = students[i];
              enrollPromises.push(
                StudentService.EnrollStudent(currentStudent, [createdOffer.id])
              );
              return Promise.all(enrollPromises);
            }
          })
          .then(() => {
            this.waitingResponse = false;
          });
      });
    },

    async createHomework() {
      const homework = generateHomework(this.selectedOffer);
      this.waitingResponse = true;

      HomeworkService.GiveHomework(homework)
        .then(homework => {
          this.homeworks.push(homework);
          return HomeworkService.GetAssignments(homework.id);
        })
        .then(() => {
          this.waitingResponse = false;
        });
    },

    async createStudents() {
      this.waitingResponse = true;
      axios.post("/api/test/students").then(() => {
        this.waitingResponse = false;
      });
    },

    async createTeachers() {
      this.waitingResponse = true;
      axios.post("/api/test/teachers").then(res => {
        this.teachers.push(res.data);
        this.waitingResponse = false;
      });
    },

    async answerAssignments() {
      this.waitingResponse = true;
      HomeworkService.GetHomework(this.selectedHomework)
        .then(homework => {
          return HomeworkService.GetAssignments(homework.id);
        })
        .then(assignments => {
          let assignmentPromises = [];
          for (let i = 0; i < assignments.length; i++) {
            const currentAssignment = assignments[i];
            let answers = [];
            for (let i = 0; i < 5; i++) {
              let rnd = Math.floor(Math.random() * 5);
              answers.push(rnd);
            }
            
            assignmentPromises.push(
              axios.patch("/api/assignments/" + currentAssignment.id, {
                answers
              })
            );
          }

          return Promise.all(assignmentPromises);
        })
        .then(() => {
          this.waitingResponse = false;
        });
    },

    async reviewAssignments() {
      this.waitingResponse = true;
      HomeworkService.GetHomework(this.selectedHomework)
        .then(homework => {
          return HomeworkService.GetAssignments(homework.id);
        })
        .then(assignments => {
          let assignmentPromises = [];
          for (let i = 0; i < assignments.length; i++) {
            const currentAssignment = assignments[i];

            assignmentPromises.push(
              HomeworkService.ReviewAssignment(currentAssignment)
            );
          }

          return Promise.all(assignmentPromises);
        })
        .then(() => {
          this.waitingResponse = false;
        });
    }
  }
};

function generateHomework(offerId) {
  let questions = [];

  for (let i = 0; i < 5; i++) {
    questions.push({
      text: "Example question",
      answerType: "multiple",
      point: 20,
      answer: Math.floor(Math.random() * 4),
      choices: ["Choice A", "Choice B", "Choice C", "Choice D"]
    });
  }

  const homework = {
    questions,
    offerId
  };

  return homework;
}
</script>

