import axios from 'axios'
import qs from 'qs'

const url = '/api/offers/'

class OfferService {
    static async CreateOffer(offerBody) {
        const response = await axios.post(url, { offerBody })
        return response.data
    }

    static async GetOffer(offerId) {
        const response = await axios.get(url + offerId)
        return response.data
    }

    static async GetOffers() {
        const response = await axios.get(url);
        return response.data;
    }

    static async GetHomeworks(offerId) {
        const response = await axios.get(url + offerId + '/homeworks')
        return response.data
    }

    static async DeleteOffer(offerId) {
        const response = await axios.delete(url + offerId)
        return response.data
    }

    static async GetEnrolleds(offerId, attributes) {
        const response = await axios.get(url + offerId + '/enrolleds', {
            params: {
                attributes
            },
            paramsSerializer: params => {
                return qs.stringify(params)
            }
        })
        return response.data
    }

    static async UnenrollStudents(offerId, students) {
        const response = await axios.delete(url + offerId + '/unenroll', students)
        return response.data
    }

    static async UploadFile(offerId, file) {
        var formData = new FormData();
        formData.append("file", file);

        const response = await axios.post(url + offerId + '/upload', formData, { headers: { 'Content-Type': 'multipart/form-data' } });
        return response.data;
    }

    static async GetEnrolledAssignmentsMarks(enrolledId) {
        const response = await axios.get(`/api/enrolleds/${enrolledId}/assignments/marks`);
        return response.data;
    }
}

export default OfferService;